      </section>
    </main>
  </template>
  
  <script>
  export default {
    name: 'MainContent',
    methods: {
      handleMouseMove(event) {
        const image = this.$refs.rotatableImage;
        const { clientX, clientY } = event;
        const { offsetWidth: width, offsetHeight: height } = image;
  
        const rotateX = ((clientY / height) - 0.5) * 10; // Efecto de rotación en X
        const rotateY = ((clientX / width) - 0.5) * 10;  // Efecto de rotación en Y
  
        image.style.transform = `rotateX(${rotateX}deg) rotateY(${rotateY}deg)`;
      }
    },
    mounted() {
      document.addEventListener('mousemove', this.handleMouseMove);
    },
    beforeUnmount() {
      document.removeEventListener('mousemove', this.handleMouseMove);
    }
  }
  </script>
  
  <style scoped>
