<template>
  <section class="comments">
    <h2>Comentarios</h2>
    <div class="comment-form">
      <!-- Campo de Nombre de Usuario -->
      <input v-model="username" type="text" placeholder="Nombre de Usuario" class="input-field" />
      
      <!-- Campo de Correo Electrónico -->
      <input v-model="email" type="email" placeholder="Correo Electrónico" class="input-field" />
      
      <!-- Campo para el Comentario -->
      <textarea v-model="newComment" placeholder="Escribe tu comentario..." rows="4"></textarea>
      
      <!-- Botones -->
      <div class="button-group">
        <button @click="addComment">Enviar</button>
        <button @click="fetchComments">Consultar</button>
        <button @click="updateComment">Actualizar</button>
        <button @click="deleteComment">Eliminar</button>
      </div>
    </div>

    <!-- Lista de Comentarios -->
    <div class="comment-list">
      <div v-for="(comment, index) in comments" :key="index" class="comment">
        <p>{{ comment.comentario }} <br> <small>{{ comment.nombreUsuario }} - {{ comment.correo }}</small></p>
      </div>
    </div>

    <!-- Mensaje de Estado -->
    <div v-if="statusMessage" class="status-message">
       {{ statusMessage }}
    </div>
  </section>
</template>

<script>
import axios from 'axios';

export default {
  name: 'UserComments',
  data() {
    return {
      username: '',
      email: '',
      newComment: '',
      comments: [],
      statusMessage: '' // Mensaje de estado en pantalla
    };
  },
  methods: {
    // Agregar un nuevo comentario
    addComment() {
      if (this.newComment.trim() !== '' && this.username.trim() !== '' && this.email.trim() !== '') {
        const newComment = {
          nombreUsuario: this.username, 
          correo: this.email,           
          comentario: this.newComment.trim()
        };

        console.log("📢 Enviando al backend:", newComment); 

        axios.post('https://serviceswebactivity-production.up.railway.app/comentarios', newComment)
          .then(response => {
            this.comments.push(response.data);
            this.newComment = '';
            this.username = '';
            this.email = '';

            this.statusMessage = '✅ Comentario agregado correctamente!';
            alert(this.statusMessage); // Mensaje emergente
            console.log('✅ Comentario agregado:', response.data);

            setTimeout(() => this.statusMessage = '', 3000);
          })
          .catch(error => {
            this.statusMessage = '❌ Error al agregar comentario: ' + error.message;
            alert(this.statusMessage);
            console.error('❌ Error al agregar comentario:', error);
          });
      } else {
        this.statusMessage = '⚠️ Todos los campos son obligatorios';
        alert(this.statusMessage);
      }
    },

    // Obtener todos los comentarios
    fetchComments() {
  axios.get(`https://serviceswebactivity-production.up.railway.app/comentarios/${this.username}`)
    .then(response => {
      if (response.data) {
        this.username = response.data.nombreUsuario;
        this.email = response.data.correo;
        this.newComment = response.data.comentario;
        this.statusMessage = '📌 Comentario cargado correctamente';
        alert(this.statusMessage);
      } else {
        this.statusMessage = '⚠️ No se encontró un comentario para este usuario';
        alert(this.statusMessage);
      }
      setTimeout(() => this.statusMessage = '', 3000);
    })
    .catch(error => {
      this.statusMessage = '❌ Error al consultar el comentario';
      alert(this.statusMessage);
      console.error('❌ Error al consultar el comentario:', error);
    });
},


    // Actualizar un comentario
    updateComment() {
  if (!this.username.trim()) {
    this.statusMessage = '⚠️ Ingresa el nombre de usuario para actualizar';
    alert(this.statusMessage);
    return;
  }

  const updatedComment = {
    nombreUsuario: this.username,
    correo: this.email,
    comentario: this.newComment.trim()
  };

  axios.put(`https://serviceswebactivity-production.up.railway.app/comentarios/${this.username}`, updatedComment)
    .then(response => {
      this.statusMessage = '🔄 Comentario actualizado correctamente';
      alert(this.statusMessage);
      console.log('Comentario actualizado:', response.data);
      this.fetchComments();
      setTimeout(() => this.statusMessage = '', 3000);
    })
    .catch(error => {
      this.statusMessage = '❌ Error al actualizar comentario';
      alert(this.statusMessage);
      console.error('❌ Error al actualizar comentario:', error);
    });
},


    // Eliminar un comentario
    deleteComment() {
  if (!this.username.trim()) {
    this.statusMessage = '⚠️ Ingresa el nombre de usuario para eliminar';
    alert(this.statusMessage);
    return;
  }

  axios.delete(`https://serviceswebactivity-production.up.railway.app/comentarios/${this.username}`)
    .then(response => {
      this.statusMessage = '🗑️ Comentario eliminado correctamente';
      alert(this.statusMessage);
      console.log('Comentario eliminado:', response.data);
      this.fetchComments();
      this.username = ''; 
      this.email = '';    
      this.newComment = ''; 
      setTimeout(() => this.statusMessage = '', 3000);
    })
    .catch(error => {
      this.statusMessage = '❌ Error al eliminar comentario';
      alert(this.statusMessage);
      console.error('❌ Error al eliminar comentario:', error);
    });
  },

}
};
</script>

<style scoped>
.comments {
  width: 100%;
  max-width: none;
  padding: 100px 0;
  margin-top: 10px;
  background-color: #23192b;
  text-align: center;
  box-shadow: 0 4px 8px rgba(255, 255, 255, 0.1);
}

.comments h2 {
  color: white;
  margin-top: -50px;
  font-size: 24px;
}

.comment-form .input-field {
  width: 80%;
  padding: 10px;
  margin: 10px auto;
  border-radius: 20px;
  border: none;
  font-size: 16px;
  display: block;
  transition: box-shadow 0.3s ease;
}

.comment-form .input-field:focus {
  box-shadow: 0 0 12px rgba(255, 223, 0, 1); /* Luz dorada intensa al enfocar */
}

.comment-form textarea {
  width: 80%;
  height: 150px;
  padding: 10px;
  border-radius: 20px;
  border: none;
  display: block;
  margin: 0 auto;
  font-size: 16px;
  transition: box-shadow 0.3s ease;
}

.comment-form textarea:focus {
  box-shadow: 0 0 12px rgba(255, 223, 0, 1); /* Luz dorada intensa al enfocar */
}

.button-group {
  margin-top: 30px; /* Más espacio por encima para que los botones queden más abajo */
}

.button-group button {
  padding: 12px 24px;
  background-color: #000;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 20px;
  font-size: 16px; /* Tamaño de fuente normal */
  font-family: 'Poppins', sans-serif;
  transition: transform 0.2s ease-in-out, box-shadow 0.3s ease;
  margin: 5px;
}

.button-group button:hover {
  transform: scale(1.1);
  background-color: #000000;
  box-shadow: 0 0 15px rgba(255, 0, 242, 0.8); /* Luz dorada al hacer hover */
}

.comment-list {
  margin-top: -70px;
}

.comment {
  background: #333;
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
  text-align: left;
  color: white; /* Cambia el color del texto a blanco */
}

.status-message {
  margin-top: 20px;
  padding: 10px;
  background-color: #f0f0f0;
  border-radius: 5px;
  color: #333;
  font-weight: bold;
}

.status-message {
  background-color: #d4edda;  /* Color de fondo verde para éxito */
  color: #155724;             /* Texto verde para éxito */
}

.status-message.error {
  background-color: #f8d7da;  /* Color de fondo rojo para error */
  color: #721c24;             /* Texto rojo para error */
}

</style>







  